import React, {Component} from "react";
import {Link } from "react-router-dom";
import './App.css';
import Autumn1 from './Beautiful photos/autumn1.b6a3ffda.jpg';
import Autumn2 from './Beautiful photos/autumn2.c9972518.jpg';
import Autumn3 from './Beautiful photos/autumn4.f37d4e6d.jpg';

   class Autumn extends Component {
       render(){
        return (
            <div>
             <nav class="navbar navbar-expand-lg navbar-light bg-light">
                <div class="container-fluid">
                    <Link to="/">
                        <p class="navbar-brand">Mountain Photos</p>
                    </Link>
                    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span class="navbar-toggler-icon"></span>
                    </button>
                    <div class="collapse navbar-collapse" id="navbarNav">
                    <ul class="navbar-nav">
                        <li class="nav-item">
                        <Link to="/winter">
                        <p class="nav-link active" aria-current="page">Winter Photos</p>
                        </Link>
                        </li>
                        <li>
                        <Link to="/autumn">
                        <p class="nav-link active" aria-current="page">Autumn Photos</p>
                        </Link>
                        </li>
                        <li>
                        <Link to="/spring">
                        <p class="nav-link active" aria-current="page">Spring Photos</p>
                        </Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>

                <div>
                  <h1>Autumn Photos</h1>
                </div>  

                  <div id="carouselExampleControls" class="carousel slide" data-bs-ride="carousel">
            <div class="carousel-inner">
              <div class="carousel-item active">
                <img src={Autumn1} class="d-block w-100" alt="Winter" />
              </div>
              <div class="carousel-item">
                <img src={Autumn2} class="d-block w-100" alt="Winter" />
              </div>
              <div class="carousel-item">
                <img src={Autumn3} class="d-block w-100" alt="Winter" />
              </div>
            </div>
            <a class="carousel-control-prev" href="#carouselExampleControls" role="button" data-bs-slide="prev">
              <span class="carousel-control-prev-icon" aria-hidden="true"></span>
              <span class="visually-hidden">Previous</span>
            </a>
            <a class="carousel-control-next" href="#carouselExampleControls" role="button" data-bs-slide="next">
              <span class="carousel-control-next-icon" aria-hidden="true"></span>
              <span class="visually-hidden">Next</span>
            </a>
          </div>
        </div>
          );  
       }
    }

    export default Autumn;